body {
  margin: 0;
  padding: 15px;
  background: radial-gradient(circle at top left, #EAEAFE 0, #DDF6F3 100%);
}
.App {
  display: flex;
  margin: 0;
  padding: 0;
  flex-direction: column;
  align-items: center;
}
.container {
  width: calc(100vw -  30px);
  max-width: 30rem;
}
header {
  font-family: Roboto, sans-serif;
  text-align: center;
  padding: 50px 0 25px;
}
header h1 {
  font-weight: 700;
  font-size: 2rem;
  color: #1F2261;
  line-height: 1.2;
  margin: 0;
}
header h2 {
  font-weight: 400;
  color: #808080;
  margin: 0;
  font-size: 1.5rem;
  margin-top: 20px;
}


.currency-block {
  background-color: white;
  border-radius: 20px;
  padding: 20px;
}
.currency-table {
  display: flex;
  justify-content: space-between;
  font-family: Roboto, sans-serif;
}
.currency-image {
  width: 30%;
  aspect-ratio: 1 / 1;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.currency-table select {
  width: 60%;
  color: #26278D;
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  border: none;
  outline: none;
  background: none;
}
.currency-choose {
  font-family: inherit;
  display: flex;
  justify-content: space-around;
  width: 45%;
}
.currency-number {
  width: 45%;
  border-radius: 7px;
  background-color: #EFEFEF;
  border: none;
  outline: none;
  font-size: 20px;
  font-weight: 600;
  text-align: right;
  color: #3C3C3C;
  padding: 10px;
}
.currency-block h3 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  color: #989898;
  margin: 0px 0px 10px;
}

.currency-block__split {
  width: 100%;
  height: 1px;
  background-color: #E7E7EE;
  margin: 60px 0px 50px;
  position: relative;
}
.split-image {
  width: 15%;
  border-radius: 50%;
  aspect-ratio: 1 / 1;
  background-color: #26278D;
  position: absolute;
  left: 42.5%;
  top: 0%;
  transform: translate(0, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
}